.calendar {
    overflow:auto;
    margin: auto;
    align-items: center;
    max-width: 80%;
    display: flex;
    flex-direction: column;
    border-style: solid;
    align-items: center;
    background-color: rgb(222, 246, 255);
    border-color: #3d3d3d;
    border-width:thin;


}

.calendar-header {
    text-align: center;
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    margin-bottom: 20px;
    font-size: large;
}

.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 10px;
    width: 100%;
    max-width: 1200px;

}

.calendar-day {
    padding: 10px;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    min-height: 5em;
    position: relative;
}

.date-number {
    font-size: 1.2em;
    font-weight: bold;
    color: #000;
}

.calendar-day .events {
    margin-top: 15px;
}

.calendar-day .event {
    padding: 5px;
    margin-top: 5px;
    border-radius: 3px;
    font-size: 0.75em;
    text-align: center;
}

.calendar-day.empty {
    background-color: white;
    border: 1px solid #ddd;;


}

.event.tourney{
    background-color: #fae3fd;
    border: 1px solid #f990d8;
}

.event.practice {
    background-color: #67d8fe;
    border: 1px solid #05c0ff;
}

.event.depart {
    background-color: rgb(255, 241, 114);
    border: 1px solid rgb(255, 208, 0);
}

.event.other {
    background-color: rgb(157, 255, 159);
    border: 1px solid rgb(9, 255, 14);
}

.event.lobster {
    background-color: rgb(252, 183, 146);
    border: 1px solid rgb(255, 153, 0);
}
button {
    font-weight: bold;
    width: 5.5em;
    background-color: white;
    border-radius: 5px;
    border-color: #ddd;
    border-width: thin;
    border-style: solid;
    display: flex; 
    align-items: center; 
    justify-content: center; 
}


@media screen and (max-width: 768px) {
    .calendar-header {
        font-size: small;
    }

    .calendar-grid {
        grid-template-columns: repeat(7, 1fr);
        gap: 5px;
    }

    .calendar-day {
        padding: 5px;
        min-height: 60px;
    }

    .date-number {
        font-size: 1em;
    }

    .calendar-day .event {
        font-size: 0.65em;
    }
}

@media screen and (max-width: 480px) {

    .calendar-header {
        padding: 4px;
        min-height: 50px;
        font-size: 0.9em;
        text-align: center;
        color: #3d3d3d;
    }

    .calendar-grid {
        grid-template-columns: repeat(7, 1fr); 
        gap: 5px;
    }

    .calendar-day {
        padding: 4px;
        min-height: 50px;
    }

    .date-number {
        font-size: 0.9em;
    }

    .calendar-day .event {
        font-size: 0.6em;
    }

    button {
        width: 60px;
        font-size: 0.75em;
        padding: 2px;
    }
}